.bigImageWrapper {
    margin-bottom: 20px;
    width: 100%;
    height: 200px;
    overflow: hidden;
}
.bigImageWrapper img {
    width: 100%;
}

.smallImage {
    width: 150px;
    height: 150px;
}

.descriptionBlock {
    display: grid;
    grid-template-columns: 150px 1fr;
    grid-gap: 20px;
}